import React,{Component,Fragment} from 'react';
import {Container,Row,Col,Navbar,Nav,NavDropdown} from 'react-bootstrap';
import {Link} from 'react-scroll';
import './NavBar.css';
class NavBar extends Component{
    render(){
        return(
            <Fragment>
                <Container>
                    <nav className="navbar fixed-top navbar-expand-md bg-success  navbar-dark justify-content-between">
                        <a className="navbar-brand" href="#">Navbar</a>

                        <button className="navbar-toggler" type="button" data-toggle="collapse"
                                data-target="#collapsibleNavbar">
                            <span className="navbar-toggler-icon"></span>
                        </button>

                        <div className="collapse justify-content-end navbar-collapse" id="collapsibleNavbar">
                            <ul className="navbar-nav">
                                <li className="nav-item">
                                    <Link className="nav-link text-uppercase" activeClass="active" to="home" smooth={true} duration={500} spy={true}>Home</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link text-uppercase" activeClass="active" to="why" smooth={true} duration={500} spy={true}>Why it now?</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link text-uppercase" activeClass="active" to="feature" smooth={true} duration={500} spy={true}>Feature</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link text-uppercase" activeClass="active" to="feature" smooth={true} duration={500} spy={true}>Demo</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link text-uppercase" activeClass="active" to="feature" smooth={true} duration={500} spy={true}>Screenshot</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link text-uppercase" activeClass="active" to="contact" smooth={true} duration={500} spy={true}>Contact</Link>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </Container>
            </Fragment>
        );
    }
}


export default NavBar;
