import React,{Component,Fragment} from 'react';
import {Container,Button} from 'react-bootstrap';
import './TopBanner.css';
import  TopBannerImage  from '../../assets/images/topbanner_front.png';


class TopBanner extends Component{

    render(){
        return (
            <Fragment>
                <Container>
                    <div className="topBannerImage">
                        <div className="topBannerOverlay d-flex align-items-center justify-content-center">
                            <div className="topBanner">
                                <h1 className="text-center text-white text-lighter">IT NOW — Warranty and Inventory Tracking System</h1>
                                <p className="underHeading text-center invert">The best solution to track warranty and inventory for IT and related items</p>
                                <div className="text-center">
                                    <img src={TopBannerImage} style={{height: "55vh", width: "90vh"}}></img>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="background-dark topBannerBottom d-flex justify-content-center align-items-center">
                            <div className="btn-group">
                                <Button className="btn btn-success shadow">Live Demo</Button>
                                <Button className="btn btn-danger marginLeft3 shadow">Buy For Only 65$</Button>
                            </div>
                    </div>
                </Container>
            </Fragment>
        );
    }
}


export default TopBanner;