import React, {Component, Fragment} from 'react';
import './Footer.css';
import {Col, Container, Row} from "react-bootstrap";
class Footer extends Component{
    render(){
        return(
            <Fragment>
                <Container>
                    <div id="footer" className="section-divide-footer" style={{positin: 'relative', paddingLeft: '30px', paddingRight: '30px'}}>
                        <Row>
                            <Col lg={4} md={6} sm={12}>
                                <div className="footerDesc">
                                    <div className="header marginBottom3 text-white d-flex align-items-center">
                                         <div className="brand-icon pr-2">
                                             <i className="fab fa-accusoft"></i>
                                         </div>
                                        <h6 className="text-uppercase">It now</h6>
                                    </div>
                                    <p style={{color: '#80909A'}}>
                                        IT NOW is a multi purpose warranty and inventory tracking system for technology and related item.You can easily track and monitor assets warranty validation by this application.
                                        It is simple and easy to use and maintenance.
                                        <br></br>
                                        <b>AND..</b>
                                        <br></br>
                                        You can also note and store IP address and access details to future use.
                                    </p>
                                </div>
                            </Col>
                            <Col lg={3} md={6} sm={12}>
                                <div className="footerAbout">
                                    <div className="header marginBottom3 text-white d-flex align-items-center">
                                        <h6 className="text-uppercase">About</h6>
                                    </div>
                                    <ul className="navbar-nav" style={{color: '#80909A'}}>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#">Spa Green Creative</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#">Developers</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#">Website Link 3</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#">Why use us?</a>
                                        </li>
                                    </ul>
                                </div>
                            </Col>
                            <Col lg={3} md={6} sm={12}>
                                <div className="footerHelp">
                                    <div className="header marginBottom3 text-white d-flex align-items-center">
                                        <h6 className="text-uppercase">Need Help??</h6>
                                    </div>
                                    <ul className="navbar-nav" style={{color: '#80909A'}}>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#">Documentation</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#">FAQ</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#">Blog</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#">Contact Us</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#">Terms Of Services</a>
                                        </li>
                                    </ul>
                                </div>
                            </Col>
                            <Col lg={2} md={6} sm={12}>
                                <div className="footerHelp">
                                    <div className="header marginBottom3 text-white d-flex align-items-center">
                                        <h6 className="text-uppercase">Social Link</h6>
                                    </div>
                                    <ul className="navbar-nav" style={{color: '#80909A'}}>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#"><i className="fab fa-facebook pr-2"></i>Facebook</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#"><i className="fab fa-youtube pr-1"></i> Youtube</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#"><i className="fab fa-twitter pr-2"></i>Tweeter</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" href="#"><i className="fab fa-github pr-2"></i>Github</a>
                                        </li>
                                    </ul>
                                </div>
                            </Col>
                        </Row>
                    </div>
                    <div className="footerCopy">
                        <p>© 2016 Spa Green Creative. All right reserved in the Bangladesh and other countries.</p>
                    </div>
                </Container>
            </Fragment>
        );
    }
}

export default Footer;