import react, {Component, Fragment} from 'react';
import './Contact.css';
import {Container, Row, Col, Button} from 'react-bootstrap';

class Contact extends Component{
    constructor(props) {
        super(props);
    }

    render() {
        return(
            <Fragment>
                <Container>
                    <div id={this.props.navId}>
                        <Row className="align-items-center">
                            <Col md={8}>
                                <div className="contactFormWrap section-divide">
                                    <div className="contactFormHeader d-flex ">
                                        <div className="icon mt-1">
                                            <i className="far fa-paper-plane text-info"></i>
                                        </div>
                                        <div className="text pl-2">
                                            <div className="text-bolder mb-1">CONTACT US</div>
                                            <div className="underHeading">Let us know if you have question or suggestions</div>
                                        </div>
                                    </div>
                                    <div className="form">
                                        <Row>
                                            <Col sm={6}>
                                                <div className="form-group">
                                                    <input type="text" id="name" placeholder="Enter Your Name" className="f-14 form-control"/>
                                                </div>
                                            </Col>
                                            <Col sm={6}>
                                                <div className="form-group">
                                                    <input type="text" id="name" placeholder="Enter Your Name" className="f-14 form-control"/>
                                                </div>
                                            </Col>
                                            <Col>
                                                <textarea rows={5} placeholder="enter your message" className='form-control' id='message'>

                                                </textarea>
                                            </Col>
                                        </Row>
                                            <Button className="btn btn-success mt-3">Send Message</Button>
                                    </div>
                                </div>
                            </Col>
                            <Col md={4}>
                                <div className="contactDescHeadeer d-flex">
                                    <div className="icon">
                                        <i className="far fa-life-ring mt-1 text-success"></i>
                                    </div>
                                    <div className="text pl-2">
                                        <div className="text-bolder mb-1">BEST SUPPORT EVER</div>
                                        <div className="underHeading">Free support for all customers</div>
                                    </div>
                                </div>
                                <div>
                                    <p>We provide absolutely free support for our customers.</p>

                                    <p>All updates of our template also absolutely free, and can be downloaded from theme forest after release.</p>

                                    <p>Also do not hesitate to send your questions or suggestions. We appreciate your help, and we will do our best to make template better.</p>
                                </div>
                                <div className="text-left">
                                    <Button className="btn btn-danger text-uppercase">Buy it for only 65$</Button>
                                </div>
                            </Col>
                        </Row>
                    </div>
                </Container>
            </Fragment>
        )
    }
}

export default Contact;